/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg); }

.webp .inicio {
  background-image: url(../img/header.webp); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .contenido-header {
        min-height: 70rem; } }

.contenido-header {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  @media (min-width: 768px) {
    .contenido-header .contenido-header {
      text-align: left; } }
  .contenido-header h1 {
    color: #FFFFFF;
    padding-bottom: 2rem;
    max-width: 60rem;
    line-height: 2;
    text-align: left;
    font-weight: 700; }

/** Footer **/
.site-footer {
  background-color: #333333;
  margin: 0; }

.contenedor-footer {
  padding: 3rem 0;
  text-align: center; }
  @media (min-width: 768px) {
    .contenedor-footer .contenedor-footer {
      display: flex;
      justify-content: space-between; } }

.site-footer nav {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .site-footer nav {
      flex-direction: row; } }

.copyright {
  margin: 0;
  color: #FFFFFF; }

/** Secciones **/
.boton {
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  display: inline-block;
  text-align: center;
  border: none;
  display: block;
  flex: 0 0 100%; }
  @media (min-width: 768px) {
    .boton {
      display: inline-block;
      flex: 0 0 auto; } }

.boton:hover {
  cursor: pointer; }

.boton-amarillo {
  background-color: #E08709; }

.boton-verde {
  background-color: #71B100; }

.boton-rojo {
  background-color: #f02626; }

.barra {
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .barra {
      flex-direction: row; } }
  .barra .logo {
    width: 30rem; }
  @media (min-width: 768px) {
    .barra {
      display: flex;
      justify-content: space-between;
      align-items: center; } }

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0s, opacity 0.5s linear, height 0s linear; }
  @media (min-width: 768px) {
    .navegacion {
      display: flex;
      height: auto;
      visibility: visible;
      opacity: 1; } }
  .navegacion a {
    color: #FFFFFF;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center; }
    @media (min-width: 768px) {
      .navegacion a {
        font-size: 1.8rem;
        margin-right: 2rem; } }
    .navegacion a:hover {
      color: #71B100; }
    .navegacion a:last-of-type {
      margin: 0; }

/** Mobile Menu **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none; } }

.mobile-menu img {
  width: 5rem; }

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto; }

/** Iconos Nosotros **/
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr); } }

.icono {
  text-align: center; }
  .icono img {
    height: 10rem; }
  .icono h3 {
    text-transform: uppercase; }

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-start; } }

.anuncio {
  border: 1px solid #B5B5B5;
  background-color: #F5F5F5;
  margin-bottom: 2rem; }

.contenido-anuncio {
  padding: 2rem; }
  .contenido-anuncio h3, .contenido-anuncio p {
    margin: 0; }
  .contenido-anuncio .precio {
    color: #71B100;
    font-weight: 700; }

.iconos-caracteristicas {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  flex: 1;
  max-width: 500px; }

.iconos-caracteristicas li {
  /* flex: 1; */
  display: flex; }
  .iconos-caracteristicas li img {
    margin-right: 2rem; }

.ver-todas {
  display: flex;
  justify-content: flex-end; }

.anuncio .boton {
  width: 100%;
  text-transform: uppercase; }
  .anuncio .boton:hover {
    background-color: #c77808; }

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg); }

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp); }

/** Contacto Home **/
.imagen-contacto {
  background-position: center center;
  background-size: cover;
  height: 40rem;
  display: flex;
  align-items: center;
  text-align: center; }
  .imagen-contacto h2 {
    font-size: 4rem;
    font-weight: 900;
    color: #FFFFFF; }
  .imagen-contacto p {
    font-size: 1.8rem;
    color: #FFFFFF; }

/** Sección Inferior **/
@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem; } }

/*** Blog **/
.entrada-blog {
  margin-bottom: 2rem; }
  @media (min-width: 768px) {
    .entrada-blog {
      display: grid;
      grid-template-columns: 1fr 2fr;
      column-gap: 2rem; } }
  .entrada-blog:last-of-type {
    margin-bottom: 0; }

.texto-entrada a {
  color: #000000;
  text-decoration: none; }

.texto-entrada h4 {
  margin: 0;
  line-height: 1.4; }
  .texto-entrada h4::after {
    content: '';
    display: block;
    width: 15rem;
    height: .5rem;
    background-color: #71B100;
    margin-top: 1rem; }

.texto-entrada span {
  color: #E08709; }

/*** Testimoniales **/
.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem; }
  .testimonial p {
    text-align: right;
    color: #FFFFFF; }
  .testimonial blockquote {
    position: relative;
    padding-left: 5rem;
    font-weight: 300; }
    .testimonial blockquote::before {
      content: '';
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      width: 6rem;
      height: 8rem;
      position: absolute;
      left: -2rem; }

/** internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2rem;
  margin: 0;
  padding: 1rem 0 3rem 0; }

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0; }

.formulario legend {
  font-size: 2rem;
  color: #333333; }

.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block; }

.formulario input:not([type="submit"]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem; }

.formulario input[type="radio"] {
  width: auto;
  margin: 0; }

.formulario select {
  -webkit-appearance: none;
  appearance: none; }

.formulario textarea {
  height: 20rem; }

.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0; }
  .propiedades thead {
    background-color: #71B100; }
    .propiedades thead th {
      color: #FFFFFF;
      padding: 2rem; }
  .propiedades img {
    width: 10rem; }

.alerta {
  padding: .5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 2rem 0; }
  .alerta.error {
    background-color: #f02626; }
  .alerta.exito {
    background-color: #71B100; }
